import React, { memo } from 'react';

import { getSizeImage } from "@/utils/format-utils";

import {
  CoverWrapper
} from "./style";

export default memo(function RadioRecommendCove(props) {
  const { info } = props;

  return (
    <CoverWrapper>
      <img src={getSizeImage(info.picUrl, 150)} alt="" />
      <span href="/#" className="text-nowrap name">{info.name}</span>
      <span className="text-nowrap description">{info.desc}</span>
    </CoverWrapper>
  )
})
